<template>
  <div class="home-page">
    <div class="text-zone">
      <h1 class="blast" v-html="paper"></h1>
      <router-link to="/contacts"
        ><button class="btn-blue">Contact me!</button></router-link
      >
    </div>
    <CircleFly />
  </div>
</template>

<script>
import CircleFly from "@/components/app/CircleFly.vue";
export default {
  name: "HelloWorld",
  data: () => ({
    titleText: "Hi,*I'm Aleksandr,*Front End developer.",
    paper: "",
    count: -1,
    timer: null,
    show: false,
  }),
  components: {
    CircleFly,
  },
  mounted() {
    this.printer(); // печатаем текст при старте
  },
  methods: {
    // печатаем текст
    printer() {
      this.stoper();
      this.timer = setInterval(() => {
        this.count++;
        this.titleText[this.count] === "*"
          ? (this.paper += "<br/>")
          : this.titleText[this.count] === " "
          ? (this.paper += `<span class="space"></span><span>${
              this.titleText[this.count]
            }</span>`)
          : (this.paper += `<span>${this.titleText[this.count]}</span>`);

        if (this.count === this.titleText.length - 1) {
          clearInterval(this.timer);
        }
      }, 100);
    },
    stoper() {
      clearInterval(this.timer);
      this.paper = "";
    },
  },
};
</script>
