<template>
  <Loader v-if="loading" />
  <div v-else class="home">
    <HelloWorld />
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";

export default {
  metaInfo: {
    title: "Kritskii Aleksandr | Front End Developer",
    meta: [
      {
        name: "description",
        content: "I want to make things that make a difference.",
      },
    ],
  },
  name: "Home",
  components: {
    HelloWorld,
  },
  data: () => ({
    loading: true,
  }),
  mounted() {
    setTimeout(() => {
      this.loading = false;
    }, 500);
  },
};
</script>
