<template>
  <div class="navbar-fixed">
    <nav>
      <div class="nav-wrapper grey darken-4">
        <div class="container">
          <div class="brand-logo">KRITSKII-A</div>
          <div class="burger">
            <i v-on:click="openSidenav()" class="material-icons">menu</i>
          </div>
          <ul id="nav-mobile" class="right hide-on-med-and-down">
            <li>
              <router-link to="/">Home</router-link>
            </li>
            <li><router-link to="/about">About</router-link></li>
            <li><router-link to="/projects">Projects</router-link></li>
            <li><router-link to="/skills">Skills</router-link></li>
            <li><router-link to="/contacts">Contacts</router-link></li>
          </ul>
        </div>
      </div>
    </nav>
    <ul id="slide-out" class="sidenav">
      <li>
        <i class="material-icons right close-menu" v-on:click="closeSidenav()"
          >close</i
        >
      </li>
      <li v-on:click="closeSidenav()">
        <router-link to="/">Home</router-link>
      </li>
      <li v-on:click="closeSidenav()">
        <router-link to="/about">About</router-link>
      </li>
      <li v-on:click="closeSidenav()">
        <router-link to="/projects">Projects</router-link>
      </li>
      <li v-on:click="closeSidenav()">
        <router-link to="/skills">Skills</router-link>
      </li>
      <li v-on:click="closeSidenav()">
        <router-link to="/contacts">Contacts</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data: () => ({
    sidenavInstance: null,
  }),
  methods: {
    openSidenav() {
      var elems = document.querySelectorAll(".sidenav");
      console.log(elems);
      this.sidenavInstance = M.Sidenav.init(elems, {
        preventScrolling: true,
        draggable: true,
      });
      this.sidenavInstance[0].open();
    },
    closeSidenav() {
      this.sidenavInstance[0].close();
    },
  },
};
</script>
